import './App.css';
import React, { useState } from 'react'
import Home from './screens/HomeScreen/Home';
import { Routes, Route} from 'react-router-dom'
import PurchasesScreen from './screens/PurchasesScreen/PurchasesScreen';
import LikesScreen from './screens/LikesScreen/LikesScreen';
import CartProductCard from './components/CartProductCard/CartProductCard'


function App() {

  const CartItems = []
  const [cartItems, setCartItems] = useState(CartItems)

  function handleItemCart(id, name, price, image){
    const existingItem = cartItems.find(item => item.id === id);

    if (existingItem) {
        // Товар уже есть в корзине, можно увеличить его количество или проигнорировать
        // В данном примере, мы просто увеличим количество на 1
        const updatedCartItems = cartItems.map(item =>
            item.id === id ? { ...item, quantity: item.quantity + 1 } : item
        );

        setCartItems(updatedCartItems);
    } else {
        // Товара нет в корзине, добавляем новый элемент
        setCartItems(prevCartItems => [
            ...prevCartItems,
            { id: cartItems.length + 1, image: image, name: name, price: price, quantity: 1 },
        ]);
    }
  }
  function handleDeleteItem(id) {
    const updatedCartItems = cartItems.filter(item => item.id !== id);
    setCartItems(updatedCartItems);
  }

  const cartProducts = cartItems.map(product => <CartProductCard handleDeleteItem = {handleDeleteItem} key = {product.id} name = {product.name} price = {product.price} id = {product.id} image = {product.image}/>)


  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Home handleItemCart = {handleItemCart} cartItems = {cartItems} cartProducts = {cartProducts} handleDeleteItem = {handleDeleteItem}/>}/>
        <Route path="/profile" element={<PurchasesScreen handleItemCart = {handleItemCart}/>}/>
        <Route path="/likes" element={<LikesScreen handleItemCart = {handleItemCart}/>}/>
      </Routes>
    </div>
  );
}

export default App;
