import React, { useEffect, useState } from 'react'
import styles from './PurchasesScreen.module.scss'
import axios from 'axios'
import Header from '../../components/Header/Header'
import ProductCard from '../../components/ProductCard/ProductCard';
import { Link } from 'react-router-dom'
import arrowleft from '../../images/arrow-left.svg'
import sademoji from '../../images/sademoji.svg'
import { apiUrl } from '../../server/api';

export default function PurchasesScreen(props) {

    const [purchases, setPurchases] = useState([]);

    useEffect(() => {
        axios.get(`${apiUrl}/purchases`)
            .then(response => {
                setPurchases(response.data);
            })
            .catch(error => {
                console.error('Error fetching products:', error);
            });
    }, []);

    const Products = purchases.map(product => 
        <div key={product.id} className={styles.productBlock}>
            <ProductCard handleItemCart = {props.handleItemCart} id = {product.id} name = {product.name} price = {product.price} image = {`${apiUrl}/productImage/${product.id}`}/>
        </div>
    )
    let content;
    if(purchases.length > 0){
        content = <div className={styles.products}>
            {Products}
        </div>
    }
    else{
        content = <div className={styles.noneItem}>
            <img src={sademoji} alt="" />
            <h2>У вас нет заказов</h2>
            <span>Вы нищеброд? <br/> Оформите хотя бы один заказ.</span>
        </div>
    }



  return (
    <div className={styles.wrapper}>
        <Header />
        <div className={styles.container}>  
            <div className={styles.title}>
                <Link to='/'>
                    <button><img src={arrowleft} alt="" /></button>
                </Link>
                <h1>Мои закладки</h1>
            </div>
            {content}
        </div>
    </div>
  )
}
