import React, { useEffect, useState } from 'react'
import axios from 'axios'
import styles from './Section2.module.scss'
import glass from '../../../images/glass.svg'
import ProductCard from '../../../components/ProductCard/ProductCard'
import { ProductsBase } from '../../../components/ProductCard/Products'
import { apiUrl } from '../../../server/api'

export default function Section2({handleItemCart}) {

    const [searchInput, setSearchInput] = useState('');


    const [products, setProducts] = useState([]);

    useEffect(() => {
        axios.get(`${apiUrl}/products`)
            .then(response => {
                setProducts(response.data);
            })
            .catch(error => {
                console.error('Error fetching products:', error);
            });
    }, []);

    const filteredProducts = products.filter((product) =>
        product.name.toLowerCase().includes(searchInput.toLowerCase())
    );



    const Products = filteredProducts.map(product => 
        <div key={product.id} className={styles.productBlock}>
            <ProductCard handleItemCart = {handleItemCart} image = {`${apiUrl}/productImage/${product.id}`} name = {product.name} price = {product.price} id = {product.id}/>
        </div>
    )

    let content;
    let gg;
    if (filteredProducts.length > 0) {
        content = Products
        
    }
    if(filteredProducts.length < products.length){
        gg = <>Поиск по запросу: "{searchInput}"</>
    }
    else{
        gg = <>Все кроссовки</>
    }

  return (
    <div className={styles.section2}>
        <div className={styles.container}>
            <div className={styles.section_title}>
                <h1>{gg}</h1>
                <div className={styles.filter_input}>
                    <div><img src={glass} alt="" /></div>
                    <input type="text" placeholder='Поиск...' value={searchInput} onChange={(e) => setSearchInput(e.target.value)}/>
                </div>
            </div>
            <div className={styles.cards}>
                {content}
            </div>
        </div>
    </div>
  )
}
