import React, { useState } from 'react'
import styles from './Header.module.scss'
import logo from '../../images/logo.svg'
import cart from '../../images/cart.svg'
import heart from '../../images/heart.svg'
import user from '../../images/user.svg'
import { Link } from 'react-router-dom'
import bars from '../../images/bars.svg'
import burgerclose from '../../images/burgerclose.svg'

export default function Header({handleClickCart, cartItems}) {

    const [burger, setBurger] = useState(false)
    const burgerClassname = `${styles.burgerList} ${burger ? styles.active : ''}`;

    function handleClickBuger() {
        setBurger(!burger)
        console.log(cartItems)
    }
    if (burger) {
        document.body.classList.add('active');
    } else {
        document.body.classList.remove('active');
    }



    const cartTotalPrice = Array.isArray(cartItems)
  ? cartItems.reduce((accumulator, currentValue) => accumulator + currentValue.price, 0)
  : 0;

  return (
    <div className={styles.header}>
        <div className={styles.container}>
            <div className={styles.logo}>
                <Link to='/'>
                    <img src={logo} alt="" />
                    <div>
                        <h3>REACT SNEAKERS</h3>
                        <p>Магазин лучших кроссовок</p>
                    </div>
                </Link>
            </div>
            <div className={styles.nav_list}>
                <div onClick={handleClickCart}>
                    <img src={cart} alt="" />
                    <b>{cartTotalPrice}р.</b>
                </div>
                <Link to='/likes'>
                    <img src={heart} alt="" />
                </Link>
                <Link to='/profile'>
                    <img src={user} alt="" />
                </Link>
            </div>
            <div className={styles.burger}>
                <img onClick={handleClickBuger} src={bars} alt="" />
            </div>
            <div className={burgerClassname}>
                <div className={styles.closeBurger}><img onClick={handleClickBuger} src={burgerclose} alt="" /></div>
                <div onClick={handleClickBuger} className={styles.burgerElements}>
                    <Link to='/profile'>
                        <img src={user} alt="" />
                        <span>Профиль</span>
                    </Link>
                </div>
                <div onClick={handleClickBuger} className={styles.burgerElements}>
                    <Link to='/likes'>
                        <img src={heart} alt="" />
                        <span>Закладки</span>
                    </Link>
                </div>
                <div onClick={handleClickBuger} className={styles.burgerElements}>
                    <div onClick={handleClickCart}>
                        <img src={cart} alt="" />
                        <span>Корзина</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}
