import React, { useState, useEffect } from 'react'
import styles from './Overlay.module.scss'
import CloseButton from '../CloseButton/CloseButton'
import arrowright from '../../images/arrow-right.svg'
import box from '../../images/box.svg'
import purchaseComplete from '../../images/purchaseComplete.svg'
import axios from 'axios'
import { apiUrl } from '../../server/api'

export default function Overlay({overlayClassname, cartProducts, closeOverlay, cartItems}) {



  const [count, setCount] = useState([]);

  useEffect(() => {
    axios.get(`${apiUrl}/getCount`)
        .then(response => {
          setCount(response.data.count); // Исправление здесь
        })
        .catch(error => {
            console.error('Error fetching count:', error);
        });
}, []);

  const [isPurchaseComplete, setIsPurchaseComplete] = useState(false)

  function handleButtonClick() {
    setIsPurchaseComplete(true);
    
    // Запрос для добавления покупок в базу данных Purchases
    axios.post(`${apiUrl}/addPurchases`, cartItems)
      .then(response => {
        console.log('Покупки успешно добавлены в базу данных');
        // Добавьте здесь код для обработки успешного оформления заказа
      })
      .catch(error => {
        console.error('Ошибка при добавлении покупок:', error);
        // Добавьте здесь код для обработки ошибки
      });
      
    // Запрос для увеличения значения поля count в таблице PurchasesCount
    axios.post(`${apiUrl}/increaseCount`)
      .then(response => {
        console.log('Значение count успешно увеличено');
        // Добавьте здесь код для обработки успешного увеличения значения
      })
      .catch(error => {
        console.error('Ошибка при увеличении значения count:', error);
        // Добавьте здесь код для обработки ошибки
      });
  }

  const cartTotalPrice = cartItems.reduce((accumulator, currentValue) => {
    return accumulator + currentValue.price;
  }, 0);
  const nalogPercent = cartTotalPrice / 100 * 5

    let items = cartItems.length > 0
    let content;
    if(items){
        content = <>
        <div className={styles.products}>
              {cartProducts}
            </div>
            <div className={styles.buyBlock}>
              <div>
                <span>Итого:</span>
                <div></div>
                <b>{cartTotalPrice} руб.</b>
              </div>
              <div>
                <span>Налог 5%:</span>
                <div></div>
                <b>{Math.round(nalogPercent)} руб.</b>
              </div>
              <button onClick={handleButtonClick}>Оформить заказ <img src={arrowright} alt="" /></button>
            </div>
            </>
    }
    else{
        content = <>
            <div className={styles.noneItems}>
                <img src={box} alt="" />
                <h3>Корзина пустая</h3>
                <span>Добавьте хотя бы одну пару <br/> кроссовок, чтобы сделать заказ.</span>
            </div>
        </>
    }

  return (
    <div className={overlayClassname}>
          <div className={styles.drawer}>
            <div className={styles.title}>
              <h2>Корзина</h2>
              <CloseButton closeOverlay = {closeOverlay} />
            </div>
            {isPurchaseComplete ?
            <div className={styles.purchaseComplete}>
              <img src={purchaseComplete} alt="" />
              <h3>Заказ оформлен!</h3>
              <span>Ваш заказ #{count} скоро будет передан курьерской доставке</span>
            </div> : 
            content}
          </div>
        </div>
  )
}
