import React, { useState } from 'react'
import styles from './Home.module.scss'
import Header from '../../components/Header/Header'
import Section1 from './Section1/Section1'
import Section2 from './Section2/Section2'
import CartProductCard from '../../components/CartProductCard/CartProductCard'
import Overlay from '../../components/Overlay/Overlay'
import style from '../../components/Overlay/Overlay.module.scss'

export default function Home(props) {
  const { handleItemCart, cartItems, cartProducts, handleDeleteItem } = props


  const [overlay, setOverlay] = useState(false)
  const overlayClassname = `${style.overlay} ${overlay ? style.active : ''}`;

  function handleClickCart() {
    setOverlay(!overlay)
  }


  if (overlay) {
    document.body.classList.add('activee');
  } else {
    document.body.classList.remove('activee');
  }

  return (
    <div className={styles.wrapper}>
        <Overlay cartItems = {cartItems} cartProducts = {cartProducts} closeOverlay = {handleClickCart} overlayClassname = {overlayClassname}/>
        <Header handleClickCart = {handleClickCart} overlayClassname = {overlayClassname} cartItems = {cartItems}/>
        <Section1 />
        <Section2 handleDeleteItem = {handleDeleteItem} handleItemCart = {handleItemCart}/>
    </div>
  )
}
