import React, { useEffect, useState } from 'react'
import styles from './LikesScreen.module.scss'
import axios from 'axios'
import Header from '../../components/Header/Header'
import ProductCard from '../../components/ProductCard/ProductCard';
import { Link } from 'react-router-dom'
import arrowleft from '../../images/arrow-left.svg'
import cryemoji from '../../images/cryemoji.svg'
import { apiUrl } from '../../server/api';

export default function LikesScreen(props) {

    


    const [likes, setLikes] = useState([]);

    useEffect(() => {
        axios.get(`${apiUrl}/likes`)
            .then(response => {
                setLikes(response.data);
            })
            .catch(error => {
                console.error('Error fetching products:', error);
            });
    }, []);


    const Products = likes.map(product => 
        <div key={product.id} className={styles.productBlock}>
            <ProductCard handleItemCart = {props.handleItemCart} status = {true} id = {product.id} name = {product.name} price = {product.price} image = {`${apiUrl}/productImage/${product.id}`}/>
        </div>
    )




    let content;
    if(likes.length > 0){
        content = <div className={styles.products}>
            {Products}
        </div>
    }
    else{
        content = <div className={styles.noneItem}>
            <img src={cryemoji} alt="" />
            <h2>Закладок нет :(</h2>
            <span>Вы ничего не добавляли в закладки</span>
        </div>
    }



  return (
    <div className={styles.wrapper}>
        <Header />
        <div className={styles.container}>  
            <div className={styles.title}>
                <Link to='/'>
                    <button><img src={arrowleft} alt="" /></button>
                </Link>
                <h1>Мои закладки</h1>
            </div>
            {content}
        </div>
    </div>
  )
}
